import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import AppraisalSection2           from "./AppraisalSection2";

const PARAMS = { select_fields : ["page_two"] };
const DEFAULT_FETCH_DATA = { page_two : { environment : {}, urban_infrastructure : {}, urban_equipment : {} } };

const AppraisalSection2Container = ({ appraise = DEFAULT_FETCH_DATA }) => {
	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });


	const { page_two } = appraise;

	// ------------> useHookForm <---------------
	const defaultValues  = { ...page_two };

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;

	// ------------> submitData <---------------
	const onSubmit = handleSubmit(async (formData) => {
		const dataToSubmit = { ...formData };

		await editApprasial({
			module : `appraisals/page-two/${appraiseId}`,
			method : "PUT",
			data   : dataToSubmit,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<AppraisalSection2
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(AppraisalSection2Container, PARAMS);
