import {
	Tabs,
	HeaderPage,
	Environment,
	UrbanEquipment,
	UrbanInfrastructure,
} from "Components/Appraisal";

const tabs = [
	{
		title : "Entorno",
		Tab   : Environment,
	},
	{
		title : "Infraestructura Urbana",
		Tab   : UrbanInfrastructure,
	},
	{
		title : "Equipamiento Urbano",
		Tab   : UrbanEquipment,
	},
];

const AppraisalSection2 = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[0, 1]}
				section="body"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				section="1.4 Entorno"
				isSubmitingData={isLoading}
			/>
		</>
	);
};

export default AppraisalSection2;
